import ApiMiddleware from './api'

export async function getData() {
    const result = await ApiMiddleware('promotion/all' , null , 'GET' , {} )
    return result.body;
}

export async function postData(data) {
    const result = await ApiMiddleware('promotion' , null , 'POST' , data )
    console.log("result" , result)
    // return result.body;
}