import actions from './actions'

const initialState = {
  promotionData: [],
  promotionMessage : false
}

export default function userReducer(state = initialState, action) {
  switch (action.type) {
    case actions.SET_STATE:
      return { ...state, ...action.payload }
    case actions.SET_RESPONE:
      return { ...state, ...action.payload }
    default:
      return state
  }
}
