import { all, put, call  , takeEvery } from 'redux-saga/effects'
import { getData ,postData } from 'services/promotion'
import { notification } from 'antd'
import actions from './actions'

export function* GET_DATA() {
  const promotionData = yield call(getData)
  yield put({
    type: 'promotion/SET_STATE',
    payload: {
      promotionData
    },
  })
}
export function* POST_DATA(data) {

  const { payload } = data
  const success = yield call(postData, payload)
  if (success) {
    notification.success({
      message: 'Success',
      description: 'You have successfully adding Data Promotion!',
    })
  }
  yield put({
    type: 'promotion/SET_RESPONE',
    payload: {
      success
    }
  })
  
}

export default function* rootSaga() {
  yield all([
    takeEvery(actions.POST_DATA, POST_DATA),
    takeEvery(actions.GET_DATA, GET_DATA),
  ])
}
