import superagent from 'superagent'

function ApiMiddleware(endpoint, query=null, method=null, data = {}) {
    let rejects;
    let request;
    
    const url = `http://31.220.56.172:3001/${endpoint}` ;
    
    const headers = {
      'Content-Type': 'application/json'
    };
  
    const promise = new Promise((resolve, reject) => {
        rejects = reject;
        
        request = superagent[method.toLowerCase()](url)
            .query(query)
            .send(data)
            .set(headers)
            // .timeout(timeout)
            .end((error, res) => {
                if (error) {
                    console.log("error API")
                    // handleAPIError(error, rejects);
                } else {
                    handleAPISuccess(res, resolve);
                }
            });
    });

    promise.abort = function abort() {
        request.abort();
        rejects();
    };
    return promise;
}


function handleAPISuccess(res , resolve) {
    resolve(res)
}

export default ApiMiddleware;
